	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨看(以父亲的名义)，是一部政治电影，以爱尔兰共和军和英国当局的关系为背景，反映了那里存在的一种深刻的矛盾，以及这种矛盾对政治和人们情绪的影响。四名在社会上混日子的年轻人，被警察局指控为一次爆炸事件的主谋，警察局用了各种手法来让他们招供，最后达到了目的。并且把他们的亲属数人也投入了监狱。在真正的共和军被抓获并且交代了之后，警察局依然不为这个案件翻案，他们这些人继续待在监狱里。后来父亲死在监狱里。一位女律师，经过艰苦的努力，查出了真相，在被监禁了十五年之后，他们全部被法庭释放。经过这样一番的经历，年轻人似乎成熟起来，最后他说，一定要把真相查清。但是，似乎最后并没有一个清楚的结果。</p>\n<p>这部电影有着真实的社会背景，反映了在西方社会存在的非理性的政治和非民主的政治。这种政治的出现，均是心理扩大和社会矛盾的结果。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0161.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0161.md","frontmatter":{"title":"10月18日 星期二"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0161.md absPath of file >>> MarkdownRemark"}}

